@import '../../../common/styles/sass/variables';
@import '../../../common/styles/sass/mixins';

.form {
  background-color: $backgroundBlockColor;
  border-radius: 0.5rem;
  border: 1px solid $borderColor;
  padding: 1rem;
  margin-top: -0.375rem;

  @media screen and (min-width: 768px) {
    padding: 1.25rem;
  }

  .space {
    margin-top: 1rem;
    margin-bottom: 0;

    label {
      margin-bottom: 0.375rem;
      display: block;
      font-size: 14px;
      line-height: 1.8;
      font-weight: 400;
      color: $aColor;
    }

    .input {
      min-height: 45px;
      width: 100%;
      border-radius: 0.25rem;
      border-width: 1px;
      border-color: $borderColor;
      background-color: rgba(15, 23, 42, 0.5);
      color: rgba(191, 190, 203, 1);
      height: 2.5rem;
      padding-left: 1rem;
      padding-right: 1rem;
      font-family: inherit;
      font-size: 100%;
      line-height: inherit;
      margin: 0;
      overflow: visible;
      outline: none;

      &:focus {
        border: 1px solid $primaryColor;
        outline: none;
        transition: 0.4s ease-in-out;
      }
    }

    input:-webkit-autofill,
    input:-webkit-autofill:hover,
    input:-webkit-autofill:focus {
      -webkit-text-fill-color: rgba(191, 190, 203, 1);
      -webkit-box-shadow: 0 0 0 1000px rgba(15, 23, 42, 1) inset;
      transition: background-color 5000s ease-in-out 0s;
    }

    .error {
      margin-bottom: 0;
      color: rgba(239, 68, 68, 1);
      font-size: 14px;
      line-height: 1.8;
      text-align: center;
    }

    .textarea {
      min-height: 150px;
      vertical-align: middle;
      width: 100%;
      border-radius: 0.25rem;
      border-width: 1px;
      border-color: $borderColor;
      background-color: rgba(15, 23, 42, 0.5);
      padding: 0.625rem 1rem;
      color: rgba(191, 190, 203, 1);
      resize: vertical;
      font-family: inherit;
      font-size: 100%;
      line-height: inherit;
      overflow: auto;
      outline: none;

      &:focus {
        border: 1px solid $primaryColor;
        outline: none;
        transition: $transition;
      }
    }
  }

  .submitBtn {
    @include btnProperties();
    margin-top: calc(1rem * calc(1));
  }

  .responseSuccess {
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgba(34, 197, 94, 1);
    font-size: 14px;
    line-height: 1.8;
    text-align: center;
    background-color: rgba(34, 197, 94, 0.05);
    margin-top: calc(1rem * calc(1));
    margin-bottom: calc(1rem * 0);

    .successClose {
      color: rgba(239, 68, 68, 1);
      font-size: 20px;
      padding: 0 0 15px 12px;
      cursor: pointer;
    }
  }

  .responseError {
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgba(239, 68, 68, 1);
    font-size: 14px;
    line-height: 1.8;
    text-align: center;
    background-color: rgba(239, 68, 68, 0.05);
    margin-top: calc(1rem * calc(1));
    margin-bottom: calc(1rem * 0);

    .errorClose {
      color: rgba(239, 68, 68, 1);
      font-size: 20px;
      padding: 0 0 15px 12px;
      cursor: pointer;
    }
  }
}
